@import "../../styles/variables";

.wrapper {
  background-color: #fcfdff;

  .inner {
    display: flex;
    align-items: center;
  }
}



.left {
  margin-right: $base * 20;


  .title {
    @include quicksand;

    font-weight: $fw-bold;
    font-size: $fs-lg;
    line-height: 2;
    color: #3D4F9F;
  }

  .text {
    font-weight: $fw-regular;
    font-size: $fs-md;
    line-height: 2;
    letter-spacing: 0.03em;
  }
}


.right {
  flex-basis: 85%;

  .input {
    margin-right: $base * 4;
    padding: $base * 5.3 $base * 42 $base * 5.3 $base * 5.3;
    outline: none;
    border: none;
    background: #efeff7;
    border-radius: $base * 1.25;
    font-weight: $fw-bold;
    font-size: $fs-sm;
    color: #7a85ad;
    transition: $transition;

    &:hover {
      background-color: darken(#efeff7, 5%);
    }
  }

  .btnRight {
    padding: $base * 5 $base * 15;
    background: #4d6ce1;
    box-shadow: 0 $base * 1.25 $base * 4.25 rgba(116, 104, 255, 0.2);
    border-radius: $base * 1.25;
    color: #ffffff;

    &:hover {
      background-color: darken(#4d6ce1, 5%);
    }
  }
}